@import './vars';
@import './colors';

.button {
    font-size: $font-size;
    border: none;
    cursor: pointer;
    border-radius: calc($unit * 0.5);
    padding: 3px $unit-2;
    color: $black;
}

.button_primary {
    @extend .button;
    background-color: $primary;
    color: $white;

    &:hover {
        opacity: 0.8;
    }
}

.button_primary-second {
    @extend .button_primary;
    background-color: $pink;
}

.button_secondary {
    @extend .button;
    background-color: $white;
    border: 2px solid $black;
}

.button_ghost {
    @extend .button;
    background: none;
    color: $text;
    text-decoration: underline;
    padding: 3px 0;
}


.title {
    margin-bottom: $unit;
}

.text {
    color: $text;
}

.text_secondary {
    @extend .text;
    font-size: $font-size-secondary;
    color: $text-secondary;
}

.text_primary {
    font-size: $font-size-big;
}